//==============================================
// Widgets
//==============================================

.home-widgets {
	margin-bottom: 2rem;
}

/**
*  Mybooking Widgets integration
*
**/

/* Selector Widget on Home's Widget zone */
.home-widgets .flex-form-group-wrapper input[type=text],
.home-widgets .flex-form-group label i {
	border: 1px solid #ccc;
  width: 400%; // This is a hack!
}
.home-widgets .flex-form-group label i {
	border: 1px solid #ccc;
	border-right: 0;
}
.home-widgets .flex-form-group select {
	border: 1px solid #ccc;
	color: #eee;
}
.home-widgets .form_selector-select_label_wrap:after {
	border: 1px solid #ccc;
}

/* Mybooking Contact Widget */
.widget_mybooking_engine_contact input {
	width: 100%;
	margin-bottom: 10px;
}
.widget_mybooking_engine_contact textarea {
	width: 100%;
	min-height: 150px;
	padding-top: 1rem;
}

/**
*  WordPress Widgets integration
*
**/

/* General */
.widgettitle {
	font-size: 1.3em;
}

/* Gallery Widget */
figure.gallery-item {
	margin: 0;
}

/* Navigation Widget */
.home-widgets ul.menu,
.home-widgets ul.sub-menu{
	display: block;
	margin: 0 10px;
	padding: 0;
	list-style: none;
}

/* Search Widget */
.search-form label {
	width: 79%!important;
}
.search-form input.search-field {
	width: 100%!important;
	border: 1px solid #ccc;
	padding: 1rem;
}
.search-form  input[type="submit" i] {
	width: 20%!important;
  border-style: none;
}
.search-form [type=submit]:not(:disabled) {
	padding: 1rem;
	background-color: var(--btn-bg-color);
	color: #FFF;
  border-color: var(--btn-border-color);
  letter-spacing: 1px;
  font-weight: var(--btn-font-w);
  text-transform: var(--btn-text-transform);
}
.search-form [type=submit]:hover {
	opacity: .5;
}

/* Calendar Widget */
.wp-calendar-table {
	width: 100%;
	margin: 5px;
}

/* Categories Widget */
select#cat {
	width: 100%!important;
	border: 1px solid #ccc;
	padding: 1rem;
	background-color: #FFF;
	color: #888;
}
.home-widgets ul {
	list-style: none;
	padding: 0;
}
